html, body {
  margin: 0px;
  height: 100%;
  background-color: #2f2b3a;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.game {
  display: flex;
  flex-basis: auto;
  gap: 60px;
  align-items: center;
  margin: 0 auto;
}

.game-area {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#game-info {
  font-size: 26px;
  color: white;
  margin: 12px;
}

#game-canvas {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  border-radius: 20px;
}

.start-game-buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 36px;
}

.start-ai-game-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

/* Button design from CSS Scan */
button {
  background: #5E5DF0;
  border-radius: 999px;
  box-shadow: #5E5DF0 0 10px 20px -10px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  outline: 0 solid transparent;
  padding: 8px 18px;
  min-width: 150px;
  border: 0;
}

.button-highlight {
  background: cadetblue;
}

.ai-game-options {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-family: Inter,Helvetica,"Apple Color Emoji","Segoe UI Emoji",NotoColorEmoji,"Noto Color Emoji","Segoe UI Symbol","Android Emoji",EmojiSymbols,-apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue","Noto Sans",sans-serif;
  color: white;
}